<!--  -->
<template>
    <div id="putInProperties">
        <mu-text-field hintText="" icon="person" />
        <br />
        <mu-text-field hintText="" type="number" icon="phone" :errorText="inputErrorText" @textOverflow="handleInputOverflow" :maxLength="11" class="phoneClass" />
        <br />
        <mu-text-field hintText="" icon="vpn_key" />
        <br />
        <mu-raised-button class="demo-raised-button" label="" icon="send" primary/>

        <div class="putInProperties--bottom">
            <mu-text-field hintText="" />
            <mu-text-field hintText="" />
            <mu-text-field hintText="" fullWidth />
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            input: '',
            inputErrorText: '',
            multiLineInput: '',
            multiLineInputErrorText: ''
        }
    },
    methods: {
        handleInputOverflow (isOverflow) {
            this.inputErrorText = isOverflow ? '' : ''
        },
        handleMultiLineOverflow (isOverflow) {
            this.multiLineInputErrorText = isOverflow ? '' : ''
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../../sass/main'

#putInProperties
    min-height: 90vh
    +auto--contentBox($autoMargin,$title-color-light,$title-size,$badgeFont-color,$text-size)
    +bC($F)
    +pR($autoMargin*2)
    .mu-text-field
        width: 100%
    // 
    .putInProperties--bottom
        +REM(padding, $autoMargin)
        padding:
            left: 0
            right: 0
        //  
        .mu-text-field
            width: 45% !important
            padding: 0 2.5%
        .full-width
            width: 98% !important
            padding: 0
            padding-left: 2.5%

//  -  
.mu-text-field-help
    display: none
</style>



