import  Vue                 from 'vue'
import  Vuex                from 'vuex'
import  * as getters        from './getters'
import  * as actions        from './actions'
import  * as mutations      from './mutations'

Vue.use(Vuex)
// 
const state = {
    deviceInfo: {}                                       // 
    ,city: {                                             // 
        cityCode: '3702'
        ,cityLongitude: '120.292284'                    //  - 
        ,cityLatitude:  '36.126949'                     //  - 
    }
    ,loadingState: {
        judgeShow: true
    }                                                   // 
    //  ''
    ,globalToggle: {
        houseResourceRightDrawer: {                     // '' -  - 
            open    : false
            ,docked : true
        }
    }
    ,bannerImg_Arr: []                                  // "" - banner
    ,hotBuildingList_Arr: []                            // "" - ""
    ,hotBuildingArr: []                                 // [0]
    ,buildingNum: []                                    // 
    ,furnitureInfo: {
        appPic: '',
        code: '',
        description: '',
        furnitureSizeList: [],
        imgUrl: [],
        name: '',
        price: 0,
        type: '',
        judgeShow: true
    }
    ,researchInfo: {                                    // "" -
        IPList: [],
        address: '',
        appPic: '',
        description: '',
        name: '',
        judgeShow: true
    }
    ,officeBuilding: {                                  // "" -
        judgeShow: true,
        buildingRelationList_judgeShow: true
    }
    ,coWorking: {                                       // "" - 
        judgeShow: true,
        buildingRelationList_judgeShow: true,
        nameInfo: '0'
    }
    ,regionPointList: []                                        // '' (  )
    ,BCPointList: []                                            // '' 
    ,buildingPointList: []                                      // '' 
    ,mapView: {
         typeRegionPointList: []                                // (  )
    }
    // (  /   )
    ,searchValue: {
        // 
        cityCode:               ''                                      // code
        ,indexLevel:            'administrative'                        // : administrative; : business; : building
        ,regionCode:            ''                                      // 
        ,businessCircleCode:    ''                                      // 
        // 
        ,btype:                 'A'                                     // ( A / B  )
        ,priceDayMin:           '0'                                     // (  )
        ,priceDayMax:           ''                                      // (  )
        ,priceMonthMin:         '0'                                     // (  )
        ,priceMonthMax:         ''                                      // (  )
        ,decoration:            ''                                      // ( A:; B:; C:; D: )
        // 
        ,administrative_Arr:    []                                      // 
        ,business_Arr:          []                                      // 
        ,building_Arr:          []                                      // 
        ,openMapViewNum_Arr:    []                                      // Map,( watch )
        ,setIndexLevelNum_Arr:  []                                      // , (  )
        ,saveIndexLevelNum_Arr: []                                      // (  )
    }
    ,searchMapRequest_Arr: []                                           // ,( watch )
    //  ''   
    ,map: {
        // 
        searchParameter: {
            cityCode                : ''                    // Code
            ,regionCode             : ''                    // Code
            ,businessCircleCode     : ''                    // Code
            ,bType                  : ''                    // (  )
            ,priceDayMin            : '0'                   //  - 
            ,priceDayMax            : ''                    //  - 
            ,priceMonthMin          : '0'                   //  - 
            ,priceMonthMax          : ''                    //  - 
            ,decoration             : ''                    // ( A:; B:; C:; D: )
        }
        // 
        ,mapState: {
            indexLevel              : 'administrative'      // : administrative; : business; : building
            ,openMapNum             : []                    //  ''  - 
            ,changeIndexLevelNum    : []                    //  ''  - 
            ,requestNum             : []                    //  ''  - 
        }
        // 
        ,result: {
            administrative          : []                    // 
            ,business               : []                    // 
            ,building               : []                    // 
        }
    }
}
const store = new Vuex.Store({
    state,
    getters,
    actions,
    mutations,
})
if (module.hot) {
    module.hot.accept([
        './getters',
        './actions',
        './mutations',
    ], () => {
        store.hotUpdate({
            getters: require('./getters'),
            actions: require('./actions'),
            mutations: require('./mutations'),
        })
    })
}

export default store



