<!-- (  ) - Tab -->
<template>
    <div id="building_tabs">
        <mu-tabs :value="activeTab" @change="handleTabChange" >
            <mu-tab value="tab1" title="" @click = "showbuildingList('all')" />
            <mu-tab value="tab2" title=""   @click = "showbuildingList('building')" />
            <mu-tab value="tab3" title="" @click = "showbuildingList('coWorking')" />
            <mu-tab value="tab4" title=""     @click = "showbuildingList('coWorking')" />
            <mu-tab value="tab5" icon="search" title="" @click = 'setStateToggle()' />
        </mu-tabs>
    </div>
</template>

<script>
export default {
    data () {
        return {
            activeTab: 'tab1'
        }
    }
    ,mounted: function() {
        //  
        this.getInitInfo()
    }
    ,methods: {
        // :  $store  ''   - 
        setStateToggle () {
            let openState   = this.$store.state.globalToggle.houseResourceRightDrawer.open
                ,dockedState  = this.$store.state.globalToggle.houseResourceRightDrawer.docked

            this.$store.dispatch({
                type        : 'setHouseResourceDrawer'
                ,openState  : !openState
                ,dockedState: !dockedState
            })
        }
        ,getInitInfo() {
            // console.log( '' )
            this.$store.dispatch({
                type: 'setHouseListBType',
                cityCode: '3702'    //   
                ,page   : '1'
                ,bType  : ''
            })
        }
        ,handleTabChange(val) {
            this.activeTab = val
        }
        ,showbuildingList( bType ) {
            // , 
            const setBuildingNum = () => {
                this.$store.dispatch({                                                          // : 
                    type: 'setBuildingNum'
                })
            }
            setBuildingNum()
            if( bType == 'building' ) {
                // console.log( 'building' )
                this.$store.dispatch({
                    type: 'setHouseListBType',
                    cityCode: '3702'    //   
                    ,page   : '1'
                    ,bType  : 'A'
                })
            } else if( bType == 'coWorking' ) {
                // console.log( '' )
                this.$store.dispatch({
                    type: 'setHouseListBType',
                    cityCode: '3702'    //   
                    ,page   : '1'
                    ,bType  : 'B'
                })
            } else {
                // console.log( '' )
                this.$store.dispatch({
                    type: 'setHouseListBType',
                    cityCode: '3702'    //   
                    ,page   : '1'
                    ,bType  : ''
                })
            }
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

#building_tabs
    position: fixed
    top: 1.5rem
    z-index: 1
    width: 100%

.mu-tabs
    +bC($F)
    button
        padding: 0
        +REM(padding-left,$autoMargin/2)
        +REM(padding-right,$autoMargin/2)
        //  - 

//  ( '',  )
#rightDrawer
</style>



