 <!-- Map  -->
<template>
    <div id="map_dropDownMenu">
        <mu-dropDown-menu :value="bType" @change="changeBType">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
        </mu-dropDown-menu>
        <mu-dropDown-menu :value="dayPrice" @change="changeDayPrice" v-show="showDayPrice" >
            <mu-menu-item value="1" title="" />
            <mu-menu-item value="2" title="" />
            <mu-menu-item value="3" title="3" />
            <mu-menu-item value="4" title="3~5" />
            <mu-menu-item value="5" title="5~8" />
            <mu-menu-item value="6" title="8~10" />
            <mu-menu-item value="7" title="10" />
        </mu-dropDown-menu>
        <mu-dropDown-menu :value="monthPrice" @change="changeMonthPrice" v-show="!showDayPrice">
            <mu-menu-item value="1" title="" />
            <mu-menu-item value="2" title="" />
            <mu-menu-item value="3" title="0.5" />
            <mu-menu-item value="4" title="0.5~1.5" />
            <mu-menu-item value="5" title="1.5~3" />
            <mu-menu-item value="6" title="3~5" />
            <mu-menu-item value="7" title="5" />
        </mu-dropDown-menu>
        <mu-dropDown-menu :value="decoration" @change="changeDecoration">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
            <mu-menu-item value="3" title=""/>
            <mu-menu-item value="4" title=""/>
            <mu-menu-item value="5" title=""/>
            <mu-menu-item value="6" title=""/>
        </mu-dropDown-menu>
    </div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'

export default {
    data () {
        return {
            bType           : '1'
            ,dayPrice       : '1'
            ,monthPrice     : '1'
            ,decoration     : '1'
            ,showDayPrice   : true
        }
    }
    ,methods: {
        changeBType( value ) {                                                              // : 
            this.showDayPrice = !this.showDayPrice
            this.bType = value
            if( value == 1 ){
                let parameterObj = new Object
                parameterObj.bType = 'A'
                this.saveParameter( parameterObj )
            }else{
                let parameterObj = new Object
                parameterObj.bType = 'B'
                this.saveParameter( parameterObj )
            }
        }
        ,changeDayPrice( value ) {                                                          // : 
            this.dayPrice = value
            let parameterObj = new Object
            if( value < 3 ){
                parameterObj.priceDayMin = '0'
                parameterObj.priceDayMax = ''
                this.saveParameter( parameterObj )
            }else if( value == 3 ) {
                parameterObj.priceDayMin = '0'
                parameterObj.priceDayMax = '3'
                this.saveParameter( parameterObj )
            }else if( value == 4 ) {
                parameterObj.priceDayMin = '3'
                parameterObj.priceDayMax = '5'
                this.saveParameter( parameterObj )
            }else if( value == 5 ) {
                parameterObj.priceDayMin = '5'
                parameterObj.priceDayMax = '8'
                this.saveParameter( parameterObj )
            }else if( value == 6 ) {
                parameterObj.priceDayMin = '8'
                parameterObj.priceDayMax = '10'
                this.saveParameter( parameterObj )
            }else{
                parameterObj.priceDayMin = '10'
                parameterObj.priceDayMax = ''
                this.saveParameter( parameterObj )
            }
        }
        ,changeMonthPrice( value ) {                                                        // : 
            this.monthPrice = value
            let parameterObj = new Object
            if( value < 3 ){
                parameterObj.priceMonthMin = '0'
                parameterObj.priceMonthMax = ''
                this.saveParameter( parameterObj )
            }else if( value == 3 ) {
                parameterObj.priceMonthMin = '0'
                parameterObj.priceMonthMax = '5000'
                this.saveParameter( parameterObj )
            }else if( value == 4 ) {
                parameterObj.priceMonthMin = '5000'
                parameterObj.priceMonthMax = '15000'
                this.saveParameter( parameterObj )
            }else if( value == 5 ) {
                parameterObj.priceMonthMin = '15000'
                parameterObj.priceMonthMax = '30000'
                this.saveParameter( parameterObj )
            }else if( value == 6 ) {
                parameterObj.priceMonthMin = '30000'
                parameterObj.priceMonthMax = '50000'
                this.saveParameter( parameterObj )
            }else{
                parameterObj.priceMonthMin = '50000'
                parameterObj.priceMonthMax = ''
                this.saveParameter( parameterObj )
            }
        }
        ,changeDecoration( value ) {                                                        // : 
            this.decoration = value
            let parameterObj = new Object
            if( value < 3 ){
                parameterObj.decoration = ''
                this.saveParameter( parameterObj )
            }else if( value == 3 ) {
                parameterObj.decoration = ''
                this.saveParameter( parameterObj )
            }else if( value == 4 ) {
                parameterObj.decoration = ''
                this.saveParameter( parameterObj )
            }else if( value == 5 ) {
                parameterObj.decoration = ''
                this.saveParameter( parameterObj )
            }else{
                parameterObj.decoration = ''
                this.saveParameter( parameterObj )
            }
        }
        ,saveParameter( parameterObj ) {                                                    // : , actions$store
            this.$store.dispatch({
                type: 'saveParameter'
                ,attrObj: parameterObj
            })
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

#map_dropDownMenu
    position: fixed
    z-index: 2
    @extend %flexWrap
    width: 100%
    border-bottom: 1px solid rgba($badgeFont-color,.3)
    >.mu-dropDown-menu
        flex: 1
        +bC($F)
        border-right: 1px solid rgba($badgeFont-color,.3)
        &:last-child
            border-right: none
</style>



